
body {
    margin: 0px 10px 50px;
}

a.ignore {
    text-decoration: none !important;
    color: inherit !important;
}

.header_name {
    font-size: 27px;
    font-family: Franklin Gothic Demi, Franklin Gothic Medium, Franklin Gothic, ITC Franklin Gothic, Arial, sans-serif;
    margin-left: 5px;
    margin-right: 5px;
    text-align: center;
}

.header_other {
    font-size: 13px;
    font-family: Franklin Gothic Demi, Franklin Gothic Medium, Franklin Gothic, ITC Franklin Gothic, Arial, sans-serif;
    margin-left: 5px;
    margin-right: 5px;
    text-align: center;
}

.headerCircle {
    display: none;
    background: #FFFFFF;
    border-radius: 50%;
    z-index: 100;
}

.headerBox {
    width: 100%;
}

.headerText {
    text-transform: uppercase;
    position: relative;
    margin-left: 5px;
    margin-right: 5px;
    top: 25%;
    bottom: 25%;
}

.sectionTitle {
    font-size: 19px;
    font-family: Franklin Gothic Demi, Franklin Gothic Medium, Franklin Gothic, ITC Franklin Gothic, Arial, sans-serif;
    text-transform: uppercase;

}

.sectionBar {
    display: block;
    /*width: 90%;*/
    height: 10px;
    margin-bottom: 10px;
}

.sectionContainer {
    margin: 0 20px 0 20px;
}

.techItem {
    cursor: pointer;
    margin-bottom: 3px;
}

.techlist {
    list-style-type: none
}

.techsublist {
    list-style-type: circle;
    margin: 0 0 0 0;
}

.sectionContent {
    /*width: 90%;*/
}

.description {
    font: 13px 'Rockwell, Courier Bold, Courier, Georgia, Times, Times New Roman, serif';
}

.headline {
    font-size: 13px;
    font-family: Franklin Gothic Demi, Franklin Gothic Medium, Franklin Gothic, ITC Franklin Gothic, Arial, sans-serif;
    margin-bottom: 5px;
    text-transform: uppercase;
}

.headerIcon {
    width: 13px;
    height: 13px;
    margin-right: 5px;
}

.ft0 {
    font-size: 73px;
    font-family: Franklin Gothic Demi, Franklin Gothic Medium, Franklin Gothic, ITC Franklin Gothic, Arial, sans-serif;
    color: #ea4e4e;
}

.ft1 {
    font-size: 19px;
    font-family: Franklin Gothic Demi, Franklin Gothic Medium, Franklin Gothic, ITC Franklin Gothic, Arial, sans-serif;
}

#sidebar {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

#contentbar {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

@media only screen and (min-width: 768px) {
    .header_name {
        font-size: 33px;
        text-align: right;
    }

    .header_other {
        font-size: 15px;
        text-align: right;
    }

    #contentbar {
        width: 65%;
        display: -ms-inline-flexbox;
        display: inline-flex;
        -ms-flex-direction: column;
            flex-direction: column;
    }

    #sidebar {
        width: 35%;
        display: -ms-inline-flexbox;
        display: inline-flex;
        -ms-flex-direction: column;
            flex-direction: column;
    }

    .headerCircle {
        display: -ms-inline-flexbox;
        display: inline-flex;
    }

    .headerBox {
        margin-left: 5%;
        margin-right: 5%;
        width: 90%;
        position: absolute;
        top: 50px;
    }

    .headerText {
        text-align: right;
        margin-right: 10px;
    }
}




